* {
  box-sizing: border-box;
}

.page-body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  min-height: 100%;
  color: #ffffff;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Yeseva One";
  src: local("Yeseva One"), url("../fonts/YesevaOne-Regular.woff2") format("woff2"), url("../fonts/YesevaOne-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

header {
  background-color: #181617;
  background-image: url("../img/background/Desktop/bgc-header-desktop.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right center;
  min-height: 684px;
  padding-top: 46px;
}

.container {
  max-width: 1159px;
  margin: 0 auto;
  padding: 0;
}

.page-header__container {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  max-width: 960px;
}

.page-header__content {
  width: 338px;
  margin-top: 114px;
}

.page-header__section-description-city {
  margin-bottom: 18px;
}

.page-header__main-title {
  margin-bottom: 31px;
}

.page-header__main-button {
  margin-top: 52px;
}

.section-description {
  color: #a07B50;
  font-weight: 400;
  line-height: 24px;
}

.main-title {
  font-family: "Yeseva One", "Verdana", sans-serif;
  font-size: 52px;
  font-weight: 400;
  line-height: 60px;
}

.main-button {
  display: block;
  width: 200px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  border: 1px solid #a07B50;
  padding: 18px 35px;
  transition: background-color 0.5s ease-out;
}

.main-button:hover {
  background-color: #a07B50;
}

.main-button:active {
  opacity: 0.5;
}

.logo {
  margin: 0 auto;
}

.socials {
  position: relative;
  top: 38px;
  left: -100px;
}

.socials::before {
  position: absolute;
  content: "";
  top: -62px;
  left: -30px;
  width: 77px;
  height: 0.5px;
  background-color: rgba(255, 255, 255, 0.61);
  transform: rotate(90deg);
}

.pets {
  background-color: #1f1c1d;
  padding-top: 223px;
  padding-bottom: 83px;
}

.pets__container {
  text-align: center;
}

.pets__secondary-title {
  margin-bottom: 23px;
}

.pets__section-description {
  margin-bottom: 38px;
}

.pets__main-button {
  display: block;
  margin: 0 auto;
  margin-top: 55px;
}

.pets__slider-btn-prev,
.pets__slider-btn-next {
  display: none;
}

.pets__indicators {
  position: static;
  margin-bottom: 46px;
}

.pets__indicators [data-bs-target] {
  box-sizing: border-box;
  color: #ffffff;
  width: 75px;
  text-indent: 0;
}

.pets .carousel-item-next,
.pets .carousel-item-prev,
.pets .gallery-images.active {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 30px;
}

.secondary-title {
  font-family: "Yeseva One", "Verdana", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 41px;
}

.gallery-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 48px;
}

.gallery-list__item:not(:last-child) {
  margin-right: 43px;
}

.gallery-list__button {
  background-color: transparent;
  border: none;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.2);
}

.gallery-list__button--active {
  color: #ffffff;
}

.gallery-images__photo1 {
  grid-column: 1/2;
  grid-row: 1/2;
  place-self: start end;
  width: 519px;
  height: 214px;
  overflow: hidden;
}

.gallery-images__photo2 {
  display: grid;
  align-items: center;
  grid-column: 2/3;
  grid-row: 1/3;
  height: 486px;
  overflow: hidden;
}

.gallery-images__photo3 {
  grid-column: 1/2;
  grid-row: 2/3;
  place-self: end end;
  width: 518px;
  height: 241px;
  overflow: hidden;
}

.features {
  background-color: #1f1c1d;
}

.features__container {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 966px;
  background-image: url("../img/background/Desktop/features-bgc-mein-kun.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center 33px;
}

.features__secondary-title {
  margin-bottom: 23px;
}

.features__section-description {
  width: 745px;
  text-align: center;
  margin-bottom: 33px;
}

.features-list {
  position: absolute;
}

.features-list__item {
  position: relative;
  font-size: 14px;
  line-height: 22px;
  width: 213px;
}

.features-list__item::before {
  position: absolute;
  content: "";
  top: 0;
  right: -50px;
  width: 15px;
  height: 15px;
  background-color: #a07B50;
  border-radius: 50%;
}

.features-list__item:nth-child(1) {
  top: 324px;
  left: -444px;
}

.features-list__item:nth-child(2) {
  top: 591px;
  left: -470px;
}

.features-list__item:nth-child(3) {
  top: 369px;
  left: 468px;
}

.features-list__item:nth-child(3)::before {
  display: none;
}

.features-list__item:nth-child(4) {
  top: 539px;
  left: 427px;
}

.features-list__item:nth-child(4)::before {
  display: none;
}

.features-list__item--left-bullet::after {
  position: absolute;
  content: "";
  top: 0;
  left: -50px;
  width: 15px;
  height: 15px;
  background-color: #a07B50;
  border-radius: 50%;
}

.advantages {
  background-color: #151313;
  padding-top: 83px;
  padding-bottom: 99px;
}

.advantages__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__secondary-title {
  margin-bottom: 23px;
}

.advantages__section-description {
  margin-bottom: 47px;
}

.advantages-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  gap: 30px;
}

.advantages-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 360px;
  height: 301px;
  padding: 17px 20px;
  background-color: #1f1c1d;
}

.advantages-list__item:hover .advantages-list__link {
  background-color: #a07B50;
  transition: all 0.5s ease-out;
}

.advantages-list__item:hover .advantages-list__svg {
  stroke: #a07B50;
  fill: #ffffff;
  transition: all 0.5s ease-out;
}

.advantages-list__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  text-align: center;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease-out;
}

.advantages-list__svg {
  margin-bottom: 36px;
  stroke: #1f1c1d;
  fill: #a07B50;
  transition: all 0.5s ease-out;
}

.care {
  background-image: url("../img/background/Desktop/bgc-care.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 84px 50px;
  min-height: 685px;
}

.care__secondary-title {
  text-align: center;
  margin-bottom: 48px;
}

.care-list {
  display: flex;
  flex-flow: row wrap;
  gap: 42px;
}

.care-list__item {
  position: relative;
  width: 558px;
  min-height: 165px;
  border: 0.5px solid #a07B50;
  padding: 38px 78px 20px 59px;
  cursor: pointer;
  user-select: none;
}

.care-list__rules-item {
  margin-bottom: 15px;
}

.care-list__desc-item {
  margin-bottom: 15px;
}

.care-list__title {
  color: #a07B50;
  font-weight: 700;
  font-size: 22px;
  line-height: 25px;
  margin-bottom: 12px;
}

.care-list__desc,
.care-list__desc-show-more {
  font-size: 14px;
  line-height: 22px;
}

.care-list__desc-show-more {
  position: absolute;
  top: 164px;
  left: 0;
  border: 0.5px solid #a07B50;
  padding: 38px 78px 20px 59px;
  background-color: #181617;
  z-index: 100;
  animation: showCareItem 0.5s ease 0s 1 normal forwards;
}

.care-list__button {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #a07B50;
  width: 60px;
  height: 100%;
  border: none;
  padding: 0;
}

.care-list__svg {
  transition: all 0.5s ease-out;
}

@keyframes showCareItem {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hiddenCareItem {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(-50px);
  }
}

.remove-accordion {
  animation: hiddenCareItem 0.5s ease 0s 1 normal forwards;
}

.our-team {
  background-color: #151313;
  padding: 83px 50px 72px 50px;
}

.our-team__container {
  min-width: 1188px;
}

.our-team__secondary-title {
  margin-bottom: 23px;
  text-align: center;
}

.our-team__section-description {
  margin-bottom: 22px;
  text-align: center;
}

.main-slider__wrapper {
  max-width: 973px;
  margin: 0 auto;
}

.main-slider__item.active {
  display: flex;
  justify-content: flex-end;
  position: relative;
}

.main-slider__img-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 360px;
  height: 356px;
  overflow: hidden;
}

.main-slider__content {
  background-color: #151313;
  border: 0.5px solid rgba(160, 123, 80, 0.47);
  width: 660px;
  padding: 23px 34px 23px 26px;
  margin-left: auto;
}

.main-slider__content-wrapper {
  background-color: #1f1c1d;
  padding: 69px 98px 59px 98px;
}

.main-slider__name-employee {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #a07B50;
  margin-bottom: 10px;
}

.main-slider__employee-position {
  font-weight: 400;
  line-height: 22px;
  color: #a07B50;
  margin-bottom: 20px;
}

.main-slider__employee-quote {
  margin-bottom: 43px;
}

.main-slider__socials {
  position: initial;
  display: flex;
  flex-flow: row-reverse wrap;
  justify-content: flex-end;
}

.main-slider__socials::before {
  display: none;
}

.main-slider__socials__item:not(:first-child) {
  margin-right: 10px;
}

.main-slider__btn-prev {
  left: -60px;
}

.main-slider__btn-next {
  right: -60px;
}

.main-slider__btn-icon-prev {
  background-image: url("../img/icons/icon-btn-prev.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.main-slider__btn-icon-next {
  background-image: url("../img/icons/icon-btn-next.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.main-slider__wrapper--reviews-section {
  min-width: 1021px;
}

.main-slider__content-wrapper--reviews-section {
  background-color: transparent;
}

.main-slider__content--reviews-section {
  border: none;
  width: 640px;
}

.main-slider__name-employee--reviews-section {
  color: #ffffff;
}

.main-slider__employee-quote--reviews-section {
  font-size: 14px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.72);
}

.main-slider__btn-prev--reviews-section {
  left: -90px;
}

.main-slider__btn-next--reviews-section {
  right: -90px;
}

.faq {
  background-color: #1f1c1d;
  padding: 73px 0;
}

.faq__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.faq__secondary-title {
  margin-bottom: 23px;
}

.faq__section-description {
  margin-bottom: 53px;
}

.faq__list {
  width: 793px;
  margin: 0;
  padding: 0;
}

.faq__list-item {
  position: relative;
  border: 0.5px solid rgba(160, 123, 80, 0.47);
  padding: 15px 42px;
  margin-bottom: 37px;
  min-height: 110px;
  cursor: pointer;
  user-select: none;
  transition: all 0.5s ease-out;
}

.faq__question {
  transform: translate(0, -50%);
}

.faq__list-item::marker {
  font-weight: 700;
  font-size: 70px;
  line-height: 82px;
  color: #a07B50;
  margin-right: 32px;
  content: "0" counter(list-item) " ";
}

.faq__list-rules-item {
  margin-bottom: 15px;
}

.faq__list-rules-item-reserve {
  text-transform: uppercase;
  text-decoration: underline;
}

.faq__button {
  position: absolute;
  top: 0;
  right: 0;
  width: 104px;
  min-height: inherit;
  background-color: transparent;
  border: none;
  padding: 0;
}

.faq__button-vertical-line,
.faq__button-horizontal-line {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 42%;
  width: 18px;
  height: 2px;
  background-color: #a07B50;
  transition: all 0.5s ease-out;
}

.faq__button-vertical-line {
  transform: rotate(90deg);
}

.faq__answer-container {
  border-top: 0.5px solid rgba(160, 123, 80, 0.47);
  padding-top: 20px;
  animation: showFaqAnswer 0.5s ease 0s 1 normal forwards;
}

.faq__answer {
  margin-bottom: 15px;
}

@keyframes showFaqAnswer {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hiddenFaqAnswer {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(-50px);
  }
}

.remove-faq-answer {
  animation: hiddenFaqAnswer 0.5s ease 0s 1 normal forwards;
}

.reviews {
  background-color: #1f1c1d;
  min-height: 650px;
}

.reviews__secondary-title {
  margin-bottom: 58px;
  text-align: center;
}

.reviews__img-container {
  width: 481px;
  height: 331px;
  overflow: hidden;
  top: 24px;
  left: -17px;
}

.reviews__date-socials-container {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.reviews__date {
  font-weight: 400;
  font-size: 12px;
  margin-right: 36px;
  line-height: 18px;
  color: #a07B50;
}

.reviews__socials__item {
  position: relative;
  width: 28px;
  height: 28px;
  border: 0.5px solid #a07B50;
}

.reviews__socials__item:not(:first-child) {
  margin-right: 17px;
}

.reviews__socials-link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page-footer {
  min-height: 302px;
  background-color: #151313;
}

.page-footer__container {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  max-width: 848px;
  padding-top: 68px;
  padding-bottom: 50px;
}

.page-footer__container > * {
  margin-bottom: 20px;
}

.page-footer__container > *:not(:nth-child(4n)) {
  margin-right: 25px;
}

.page-footer__logo {
  margin: 0;
}

.page-footer__socials {
  display: flex;
  flex-flow: row wrap;
  position: initial;
}

.page-footer__socials::before {
  display: none;
}

.page-footer__socials-item:not(:last-child) {
  margin-right: 9px;
}

.page-footer__address {
  font-weight: 400;
  font-size: 14px;
  color: rgba(253, 242, 230, 0.83);
}

.page-footer__phone {
  font-weight: 700;
  font-size: 14px;
  color: #fdf2e6;
}